.timer-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 300px;
    padding: 20px;
    color: rgb(173, 136, 255);
    /* purpura custom: rgb(148, 101, 248) */
    /* purpura suave: rgb(173, 136, 255) */
    /* verdecito custom: rgb(80, 255, 185) */
}
.timer-container h4{
    text-align: left;
    margin: 5px;
    font-size: 25px;
}
.timer-container p{
    text-align: left;
    margin: 5px;
    font-size: 20px;
}
.timer-container fieldset{
    width: 280px;
}
.timer-container legend{
    font-size: 20px;
}
.timer-buttons{
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    margin-top: 10px;
}
.timer-buttons button{
    background-color: transparent;
    cursor: pointer;
    border: none;
    padding: 3px;
}
.modal-timer{
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgb(30, 12, 35);
}
.donut-meter{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 10px 0px;
}